	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>🌡23℃ 🌦</strong></p>\n<p>昨日は家族で、千葉県の斎藤さんの家に行きました。\n事情はよくわからないのですが、両親の友人らしいです。</p>\n<p>僕は弟と斎藤さんの息子さんと一緒に、近くの公園でウルトラマンごっこをして遊んでいました。\n息子さんの歳は僕と弟の間くらいでしょうか。\n彼がウルトラマン役で、弟が怪獣役、僕が光線で吹き飛ぶ山役でした。</p>\n<p>そして帰りにプラモデルを買って、ちょっとテレビゲームをやりました。\nファミコンじゃない、見たことのないゲーム機でした。</p>\n<p>帰路、親が「眠くて車で帰るのは難しい。途中で泊まろう」と言ってホテルを探していました。\nでも、探せど満室で見つからなくて、ホテルに泊まりました。</p>\n<p><strong>「子供は入ってはいけないホテル」</strong> と言われたので、係に見つからないようこっそり入りました。\n怖かったけど中はダブルベッドと化粧台のある、普通の部屋でした。</p>\n<p>そして今日、一度だけ係の目を誤魔化すためトイレに隠れたけど、特に何事もなくホテルを後にできました。</p>\n<p>今度は東京足立区の、父方の兄の家に行きました。\nそこで少しファミコンをして、夕食の後一緒にミニ四駆を作りました。</p>","frontmatter":{"cover":null,"date":"1990-11-04T21:00:00+09:00","strDate":"1990/11/4","title":"日記"}}},"pathContext":{"path":"/ja/blog/1990/11/04-diary/","langKey":"ja"}}

